.app__gallery {
    display: flex;
    flex-direction: row;
    background: var(--color-black);
    padding: 4rem 0 4rem 6rem;
}

.app__gallery-content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    min-width: 500px;
    padding: 2rem;
}

.app__gallery-content button {
    margin-top: 1rem;
}

.app__gallery-images {
    flex: 1;
    display: flex;
    flex-direction: row;
    max-width: 50%;
    position: relative;
}

.app__gallery-images_container {
    display: flex;
    flex-direction: row;
    width: max-content;
    overflow-x: scroll;
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.app__gallery-images_container::-webkit-scrollbar {
    display: none;
}

.app__gallery-images_card {
    position: relative;
    min-width: 301px;
    height: 400px;
    margin-right: 1rem;
}

.gallery__image-insta {
    position: absolute;
    color: #fff;
    font-size: 2rem;
    opacity: 0.5;
    transition: 0.5s ease;
    cursor: pointer;
}

.app__gallery-images_card img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 1;
    transition: 0.5s ease;
}

.app__gallery-images_card:hover img {
    opacity: 0.35;
}

.app__gallery-images_card:hover .gallery__image-insta {
    opacity: 1;
}

.app__gallery-images_arrow {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1rem;
    position: absolute;
    bottom: 15%;
}

.gallery__arrow-icon {
    color: var(--color-golden);
    font-size: 2rem;
    cursor: pointer;
    background: var(--color-black);
    border-radius: 6px;
    transition: 0.3s;
}

.gallery__arrow-icon:hover {
    background-color: black;
}

@media screen and (min-width: 2000px) {
    .app__gallery-content {
        min-width: 700px;
        padding-right: 1rem;
    }

    .app__gallery-images_card {
        min-width: 450px;
        height: 550px;
    }
}

@media screen and (max-width: 1150px) {
    .app__gallery {
        flex-direction: column;
    }
    .app__gallery-images {
        max-width: 100%;
        margin: 3rem 0;
    }
}

@media screen and (max-width: 850px) {
    .app__gallery {
        padding: 2rem 0 2rem 3rem;
    }
}

@media screen and (max-width: 650px) {
    .app__gallery {
        padding: 2rem 0 2rem 3rem;
    }
    .app__gallery-content {
        min-width: 100%;
        padding-left: 0;
    }

    .app__gallery-content h1 {
        font-size: 42px;
    }
    .app__gallery-images_card {
        min-width: 250px;
        height: 350px;
    }
}