.app__footer {
    width: 100%;
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    background: var(--color-black);
    padding-top: 0;
}


.app__footer-links {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-top: 3rem;
    padding: 0 2rem;
}

.app__footer-links_contanct,
.app__footer-links_logo,
.app__footer-links_work {
    margin: 1rem;
    text-align: center;
}

.app__footer-links_logo img:first-child {
    width: 210px;
    margin-bottom: 0.75rem;
}

.app__footer-links_work p:nth-child(2n + 1) {
    margin-bottom: 1rem;
}

.app__footer-links_icons {
    margin-top: 0.5rem;
}

.app__footer-links_icons svg {
    color: var(--color-white);
    margin-right: 1rem;
    font-size: 1.3rem;
    transition: 0.3s;
    cursor: pointer;
}

.app__footer-links_icons svg:hover {
    scale: 1.2;
    color: var(--color-golden);
}

.app__footer-headtext {
    font-family: var(--font-base);
    color: var(--color-golden);
    margin-bottom: 1rem;
    font-weight: 400;
    letter-spacing: 0.04em;
    font-size: 32px;
    line-height: 35px;
}

.footer__end {
    margin-top: 3rem;
}

.footer__end span {
    color: var(--color-golden);
    font-weight: 600;
}


@media screen and (min-width: 2000px) {
    .app__footer-headtext {
        font-size: 51px;
        line-height: 55px;
    }
    .app__footer-links_icons svg{
        font-size: 35px;
    }
}

@media screen and (max-width: 1150px) {
    .app__footer-links {
        align-items: center;
        flex-direction: column;
        padding: 0;
    }
    .app__footer-links_contanct,
    .app__footer-links_logo,
    .app__footer-links_work {
        margin: 2rem 0;
        width: 100%;
    }
}

@media screen and (max-width: 650px) {
    .app__footer {
        padding: 0 0 2rem 0;
    }
}

@media screen and (max-width: 350px) {
    .app__footer-links_logo img:first-child {
        width: 80%;
    }
}