.app__awards {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 2rem;
}

.app__award-card {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    width: 230px;
    margin: 1rem;
}

.app__award-card img {
    width: 50px;
    height: 50px;
}

.app__award-card_content {
    display: flex;
    flex-direction: column;
    margin-left: 1rem;
}

@media screen and (min-width: 1900px) {
    .app__award-card {
        min-width: 390px;
    }
}

@media screen and (max-width: 900px) {
    .app__wrapper_img img {
        min-width: 60%;
    }
}

@media screen and (max-width: 450px) {
    .app__award-card {
        min-width: 100%;
        margin: 1rem 0;
    }
    .app__wrapper_img img {
        height: 300px;
    }
}